var isDebug = false;
var doc = {};
var utils = require("./lib/utils")

module.exports = {
  config(obj) {
    console.log("obj", obj)
    if (obj) {
      console.log('ready', "ok");
    }
  },
  checkJsApi(obj) {
    console.log("checkJsApi", utils.isTj())
    if (utils.isTj()) {
      obj.success && obj.success()
    } else {
      obj.fail && obj.fail()
    }
  },
  onMenuShareTimeline(obj) {
    var tempObj = {
      title: '',
      link: '',
      imgUrl: '',
      desc: ''
    }
    var shareObj = Object.assign({}, tempObj, obj)
    utils.callTjBridge('onMenuShareTimeline', shareObj)
  },
  onMenuShareAppMessage(obj) {
    var tempObj = {
      title: '',
      desc: '',
      link: '',
      imgUrl: '',
      type: '',
      dataUrl: ''
    }
    var shareObj = Object.assign({}, tempObj, obj)
    utils.callTjBridge('onMenuShareAppMessage', shareObj)
  },
  onMenuShareQQ(obj) {
    var tempObj = {
      title: '',
      desc: '',
      link: '',
      imgUrl: '',
    }
    var shareObj = Object.assign({}, tempObj, obj)
    utils.callTjBridge('onMenuShareQQ', shareObj)
  },
  onMenuShareWeibo(obj) {
    var tempObj = {
      title: '',
      desc: '',
      link: '',
      imgUrl: '',
    }
    var shareObj = Object.assign({}, tempObj, obj)
    utils.callTjBridge('onMenuShareWeibo', shareObj)
  },
  onMenuShareQZone(obj) {
    var tempObj = {
      title: '',
      desc: '',
      link: '',
      imgUrl: '',
    }
    var shareObj = Object.assign({}, tempObj, obj)
    utils.callTjBridge('onMenuShareQZone', shareObj)
  },
  chooseImage(obj) {
    var tempObj = {
      count: 1, // 默认9
      sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
      sourceType: ['album', 'camera']
    }
    var shareObj = Object.assign({}, tempObj, obj)
    utils.callTjBridge('chooseImage', shareObj)
  },
  previewImage(obj) {
    var tempObj = {
      current: '', // 当前显示图片的http链接
      urls: [] // 需要预览的图片http链接列表
    }
    var shareObj = Object.assign({}, tempObj, obj)
    utils.callTjBridge('previewImage', shareObj)
  },
  uploadImage(obj) {
    var tempObj = {
      localId: '', // 需要上传的图片的本地ID，由chooseImage接口获得
      isShowProgressTips: 1, // 默认为1，显示进度提示
    }
    var shareObj = Object.assign({}, tempObj, obj)
    utils.callTjBridge('uploadImage', shareObj)
  },
  downloadImage(obj) {
    var tempObj = {
      serverId: '', // 需要下载的图片的服务器端ID，由uploadImage接口获得
      isShowProgressTips: 1, // 默认为1，显示进度提示
    }
    var shareObj = Object.assign({}, tempObj, obj)
    utils.callTjBridge('downloadImage', shareObj)
  },
  getLocalImgData(obj) {
    var tempObj = {
      localId: '', // 图片的localID
    }
    var shareObj = Object.assign({}, tempObj, obj)
    utils.callTjBridge('getLocalImgData', shareObj)
  }
}